<template>
  <div class="price-list-container">
    <div class="price-list-title" v-show="false">
      <span class="go_m-back" @click="goBackPage()">返回</span>
      稀土行情
    </div>
    <table class="table table-hover">
      <thead>
      <tr>
        <th class="xitu-name-info">名称</th>
        <!--<th>价格区间</th>-->
        <th>均价</th>
        <th>单位</th>
        <th>日期</th>
      </tr>
      </thead>
      <tbody id="price_list">
      <tr  v-for="item in priceList" @click="goPriceDetailPage(item.name)" class="title-thread">
        <td>
          <span class="xitu-name">{{item.name}}</span>
          <div :class="{'up':item.flag==='up','down':item.flag==='down'}" class="my-t-info m-t-first-info">{{item.min_quote}}～{{item.max_quote}}<span v-show="item.flag==='up'">↑</span><span v-show="item.flag==='down'">↓</span></div>
        </td>
        <!-- <td :class="{'up':item.flag==='up','down':item.flag==='down'}">{{item.min_quote}}～{{item.max_quote}}<span v-show="item.flag==='up'">↑</span><span v-show="item.flag==='down'">↓</span></td>-->
        <td :class="{'up':item.flag==='up','down':item.flag==='down' }" class="my-t-info">{{item.avg_quote}}<span v-show="item.flag==='up'" >↑</span><span v-show="item.flag==='down'">↓</span></td>
        <td class="my-t-info">{{item.unit}}</td>
        <td class="my-t-info">{{item.quote_date}}</td>
      </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
  import {getBaoJia} from '../api/pricelist'
  import timeUtil from '../utils/timeutil'
export default {
  name: 'PriceList',
  data () {
    return {
        priceList:[],
    }
  },
  mounted(){
      this.getPriceListData();
  },
  methods:{
    getPriceListData(){
      getBaoJia(timeUtil()).then((res)=>{
         this.priceList = res.data.data.detail;
      },(err)=>{

      });
    },
    /**
     * 跳转到稀土行情详情页
     * @param name
     */
    goPriceDetailPage(name){
      this.$router.push({path: `/pricedetail/${name}`, replace: true});
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  @import "../assets/css/price-list.css";
  .table-hover{
    width: 100%;
    height: auto;
  }
  .my-thread{
    width: 100%;
  }
  .xitu-name{
    display: block;
    padding: 10px 0 0 0;
  }
  .my-title-thread{
    height: 40px;
  }
  .my-t-info{
    padding: 10px 0;
  }
  .m-t-first-info{
    padding: 2px 0;
  }
  body, td, th, ul, li {
    font-family: "Helvetica Neue", Helvetica, Arial, Verdana, sans-serif;
    padding: 0;
    margin: 0;
  }

  a {
    color: #333;
    text-decoration: none;
    cursor: pointer;
  }
  .price-list-title{
    width: 100%;
    height: 40px;
    background: #003473;
    color: #fff;
    text-align: center;
    line-height: 40px;
    font-size: 18px;
  }
  .go_m-back{
    width: 40px;
    height: 40px;
    position: absolute;
    left: 25px;
    top: 0;
  }
  .go_m-back::before{
    content: "";
    width: 32px;
    height: 40px;
    background: url("../assets/images/arrow-right.png")no-repeat center;
    position: absolute;
    left: -30px;
    top: 0;
  }
</style>



