<template>
  <div class="price-detail-container">
    <div class="m-top">
      <span class="go_back" @click="goBackPage()">返回</span>
      <span class="m-top-text">{{mPriceData.name}}</span>
    </div>
    <div class="top">
      <div class="top1">
        <p class="name">{{mPriceData.name}}</p>
        <p class="from">数据来源：磁易购</p>
      </div>
      <div class="top2">
        <div class="col col-xs-3">
          <p class="tt">最高价格</p>
          <p class="fb">{{mPriceData.max_quote__max}}</p>
        </div>
        <div class="col col-xs-3">
          <p class="tt">最低价格</p>
          <p class="fb">{{mPriceData.min_quote__min}}</p>
        </div>
        <div class="col col-xs-3">
          <p class="tt">均价</p>
          <p class="fb">{{mPriceData.avg_quote__avg}}</p>
        </div>
        <div class="col col-xs-3">
          <p class="tt">单位</p>
          <p class="">{{mPriceData.unit}}</p>
        </div>
      </div>
    </div>

    <div class="chart-wrap">
      <div class="sort" id="sort">
        <div class="col-xs-3 col on">一月</div>
        <div class="col-xs-3 col">三月</div>
        <div class="col-xs-3 col">半年</div>
        <div class="col-xs-3 col">一年</div>
      </div>
      <div class="canvas-area">
        <div id="c-container">
          <line-example
            :data="mData"
            :options="options"
            :width="400"
            :height="200"
            v-show="isShowMap"
          >
          </line-example>
        </div>
      </div>
    </div>
    <div class="news-wrap">
      <div class="tt">资讯</div>
      <div class="news-list">
        <a @click="goNewDetail(item.id)" v-for="item in allLookData">
          <p class="cont">{{item.title}}</p>
          <p class="time">{{item.created_at}}</p>
        </a>
      </div>
    </div>
  </div>
</template>

<script>
  // 导入chart组件模拟数据
//  import options from '../api/data';
  import LineExample from './lineMarker.js'
  import {getMonthData} from '../api/pricelist'
  import {getMonthDataByMonthName} from '../api/pricelist'
  import {getLookAll} from '../api/pricelist'
  export default{
    components: {
      LineExample
    },
    data() {
      return {
       /* id: 'test',
        option: options.bar*/
        mData: {
         /* labels: ['2017-8-10','2017-8-11'],
          datasets: [{
            label: '稀土日均价',
            data: [12, 19, 3, 5, 2, 3,12, 19, 3, 5, 2, 3,12, 19, 3, 5, 2, 3],
            backgroundColor: '#f87979',
//            backgroundColor: '#0295f2',
            borderWidth: 1
          }]*/
        },
        options: {
          tooltips: {
            intersect: false // default: true. if true, the tooltip mode applies only when the mouse position intersects with an element. If false, the mode will be applied at all times.
          },
          animation: {
            duration: 1000  // 动画时长单位:ms
          },
          responsive: true, // 长宽，100%.如果要单设长和宽的话，要将responsive 设为false
          maintainAspectRatio: false // 保持长宽比
        },
        mPriceData:{},
        month:"1",
        mLabels:[],
        mMonthDatas:[],
        isShowMap:false,
        allLookData:[]
      }
    },
    watch:{
      $route(to){
        //对传递的id进行验证(防止篡改)
        const _this = this;
        let reg=/pricedetail\/\u4e00-\u9fa5/;
        if(reg.test(to.path)){
          _this.getDetailData(_this.$route.params.priceName);
          _this.getMonthMoreData(_this.month,_this.$route.params.priceName);
        }
      }
    },
    mounted(){
      const _this = this;
      _this.getDetailData(_this.$route.params.priceName);
      _this.getMonthMoreData(this.month,_this.$route.params.priceName);
      _this.getAllLookData();
      $('#sort .col').click(function(event) {
        $(this).addClass('on').siblings().removeClass('on');
        switch ($(this).html()){
          case "一月":
            _this.month = "1";
              break;
          case "三月":
            _this.month = "3";
            break;
          case "半年":
            _this.month = "6";
            break;
          case "一年":
            _this.month = "12";
            break;
        }
        _this.getMonthMoreData(_this.month,_this.$route.params.priceName);
      });
    },
    methods:{
      goNewDetail(newsId){
          window.location.href = `http://m.ciyigou.com/mobile/index#/newsdetail/${newsId}`;
      },
      getAllLookData(){
        getLookAll().then((res)=>{
          this.allLookData = res.data.data.detail;
        },(err)=>{

        });
      },
      goBackPage(){
        window.history.go(-1);
      },
      /**
       * 获取当月更多的稀土数据
       * @param date：月份
       * @param name:对应的关键字
       */
      getMonthMoreData(date,name){
        getMonthDataByMonthName(name,date).then((res)=>{
            this.mMonthDatas = [];
            this.mLabels = [];
            this.mMonthDatas = res.data.data.detail;
            let len = this.mMonthDatas.length;
            len > 0 ? this.isShowMap = true : this.isShowMap = false;
            var mObj = {};
            mObj.labels = [];
            mObj.datasets = [];
            var mRealData = {
              label: '稀土日均价',
              data: [],
              //设置图标显示的颜色
              backgroundColor: '#f87979',
//            backgroundColor: '#0295f2',
              borderWidth: 1
            }
            for(let i = 0 ;i<len;i++){
                this.mLabels.push(this.mMonthDatas[i].quote_date);
                mRealData.data.push(this.mMonthDatas[i].avg_quote);
            }
          mObj.datasets.push(mRealData);
          mObj.labels = this.mLabels;
          this.mData = mObj;
        },(err)=>{

        });
      },
      getDetailData(name){
        getMonthData(name).then((res)=>{
           this.mPriceData = res.data.data.detail;
        },(err)=>{

        });
      }
    }
  }
</script>

<style scoped>
  @import "../assets/css/price-detail.css";
  .top .top1{
    text-align: left;
  }
  #test {
    width: 400px;
    height: 400px;
    margin: 40px auto;
  }
  .news-list{
    text-align: left;
  }
  .m-top{
    width: 100%;
    height: 40px;
    background: #003473;
    color: #fff;
    font-size: 18px;
    line-height: 40px;
    position: relative;
  }
  .m-top-text{

  }
  .go_back{
    width: 40px;
    height: 40px;
    position: absolute;
    left: 25px;
    top: 0;
  }
  .go_back::before{
    content: "";
    width: 32px;
    height: 40px;
    background: url("../assets/images/arrow-right.png")no-repeat center;
    position: absolute;
    left: -30px;
    top: 0;
  }
</style>



